import { Component, Listen, Prop, h } from '@stencil/core';

@Component({
  tag: 'p-table',
  styleUrl: 'p-table.scss',
  scoped: true
})
export class PagedDataset {
  page: number = 0;
  id: number = 0;

  @Prop() headers: any[] = [];
  @Prop() action: boolean = false;

  @Prop() itemCount: number = 0;
  @Prop() items: any[] = [];

  @Prop() pagination: boolean = false;

  @Listen('pageChanged')
  handleSelected(event: CustomEvent) {
    this.page = event.detail
  }

  render() {
    if (this.items) {
      return (
        <div>
          <div class="container">
            <table>
              <thead>
                {
                  this.headers.map(header =>
                    <th key={header.id}>{header.name}</th>)
                }
                { this.action ? <th>Acci√≥n</th> : '' }
              </thead>
              <tbody>
                {
                  this.items.map(item =>
                    <tr key={item.id}>
                      {
                        this.headers.map(header =>
                        <td key={item.id}>{item[header.id]}</td>
                        )
                      }
                      {
                        this.action ? <td><p-button text="Ver producto" color="action" size="sm" id={item.id}></p-button></td> : ''
                      }
                    </tr>
                  )
                }
              </tbody>
            </table>
          </div>
          {this.pagination ? <p-paginator page={this.page} itemCount={this.itemCount}></p-paginator> : ''}
        </div>
      );
    }
  }

}
